
.wgu-infoclick-win {
  background-color: white;
  z-index: 2;
  width: 450px;
}
.v-card.wgu-infoclick-win {
  position: absolute;
}
.wgu-infoclick-win .v-card__title {
  display: inherit;
}
@media (max-width: 600px) {
  /* tmp. approach to position on small screens */
.v-card.wgu-infoclick-win {
    /* tmp. fix */
    left: 0 !important;
    top: 40% !important;
    width: 100%;
    max-width: 600px;
}
.wgu-infoclick-win-title {
    overflow: scroll;
    max-height: 300px;
}
}




