
table.wgu-coordstable {
  margin-top: 12px;
  width: 100%;
}
.wgu-coordstable table {
  border-radius: 3px;
  background-color: #fff;
}
.wgu-coordstable td {
  background-color: #f9f9f9;
}
.wgu-coordstable tr {
  font-size: 16px;
}
.wgu-coordstable th, .wgu-coordstable td {
  width: 200px;
  padding: 5px 5px;
}
.wgu-coordstable td.key-td {
  width: 160px;
  padding: 5px 5px;
}




