
div.ol-zoom {
  top: auto;
  left: auto;
  bottom: 3em;
  right: 0.5em;
}
div.ol-attribution.ol-uncollapsible {
  font-size: 10px;
}

/* Hover tooltip */
.wgu-hover-tooltiptext {
  width: 120px;
  background-color: rgba(211, 211, 211, .9);
  color: #222;
  text-align: center;
  padding: 5px;
  border-radius: 6px;

  /* Position the hover tooltip */
  position: absolute;
  z-index: 1;
}




