
html {
  /* otherwise we have always vertical scrollbars */
  overflow-y: hidden !important;
}

.wgu-app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  position: relative;
  width: 100%;
}
.wgu-app-embedded {
  width: 100%;
  height: 100%;
  border: 2px solid grey;
}

.wgu-map {
}
.wgu-app-embedded .wgu-map {
}

.wgu-app-logo {
  left: 8px;
  bottom: 50px;
}

.wgu-measurewin {
  left: auto !important;
  top: auto !important;
  right: 10px;
  bottom: 4.8em;
}

.wgu-layerlist {
  left: auto !important;
  top: 75px !important;
  right: 10px;
}

.wgu-footer-left {
  padding-left: 5px;
}
.wgu-footer-right {
  padding-right: 5px;
}
.wgu-footer-left a, .wgu-footer-right a {
  color: inherit;
}

.wgu-win-title {
   -moz-user-select: none;
   -khtml-user-select: none;
   -webkit-user-select: none;
   /* Introduced in IE 10, See http://ie.microsoft.com/testdrive/HTML5/msUserSelect/ */
   -ms-user-select: none;
   user-select: none;
}



